

<template web>
	<div class="side-padded">
		<back-button />
		<h1 class="h1 text-center">My Posts</h1>
		<ul>
			<ShiftListItem v-for="(shift, $index) in shifts" :key="$index" :shift="shift" :is-user="true"/>
		</ul>
		<div class="text-center">
			<button class="button button__is-inline" @click="$emit('logout')" type="button">Log Out</button>
			<router-link :to="{name: 'AdminApproval'}" v-if="store.isAdmin" class="button button__is-inline">Admin Panel</router-link>
		</div>
	</div>
</template>

<script>
	import ShiftListItem from '../components/ShiftListItem';
	import ApiService from '../services/ApiService';
	import EmsaPage from '../mixins/EmsaPage';

	export default {
		name: 'UserView',
		mixins: [EmsaPage],
		components: {
			ShiftListItem
		},
		props: {
			scrollIndex: Number
		},
		computed: {
			shifts() {
				return this.store.userList;
			}
		},
		methods: {
			showShift(index) {
				let shift = this.shifts[index];
				shift.isUser = true;
				this.$emit('shiftSelected', shift, true);
			},
			getShifts($event) {
				const notify = () => {
					$event && $event.object.notifyPullToRefreshFinished();
				};

				ApiService.getShifts({is_user: true}, shifts => {
					this.store.userList = shifts;
					notify();
				}, error => {
					if (error.response && error.response.status == 401) {
						this.$emit('logout');
					} else {
						notify();
					}
				})
			}
		},
		mounted() {
			this.getShifts();
		}
	}
</script>
